export default {
	data() {
		return {
			map: null, //初始化map
			locationState: true, //显示当前定位点
			covers: [{
				id: 1,
				latitude: null,
				longitude: null,
				title: "当前位置",
				iconPath: '../../static/mapImg/marker.png',
				width: 30,
				height: 30,
			}], //marker
			controls: [{
					id: 1,
					position: {
						left: 10,
						top: 50,
						width: 40,
						height: 40
					},
					iconPath: '../../static/mapImg/controlPosition.png',
					clickable: true
				},
				{
					id: 2,
					position: {
						left: 10,
						top: 110,
						width: 40,
						height: 40
					},
					iconPath: '../../static/mapImg/refresh.png',
					clickable: true
				},
				{
					id: 3,
					position: {
						left: 10,
						top: 170,
						width: 40,
						height: 40
					},
					iconPath: '../../static/mapImg/navigation.png',
					clickable: true
				}
			], //controls
			polyline: [{
				points: [],
				color: '#8A2BE288',
				width: "10",
				dottedLine: false,
				arrowLine: true,
			}], //画线
			marker: null, //选中的markerid
			height: {
				height: null
			}, //初始化已接订单容器的高度
			latitude: null,
			longitude: null,
			name: null,
		}
	},
	onShow() {
		setTimeout(() => {
			/* 创建地图对象 */
			this.map = uni.createMapContext('myMap');
		}, 0)
		setTimeout(() => {
			/* 将中心调到当前点（当前定位） */
			this.map.moveToLocation()
		}, 200)
		setTimeout(() => {
			/* 获取当前点的坐标并加marker */
			this.getCurrentLocation()
		}, 400)
	},
	onLoad(data) {
		console.log(data)
		this.latitude = data.lat;
		this.longitude = data.lng;
		this.name = data.name
		uni.setNavigationBarTitle({
			title: this.name
		});
	},
	methods: {
		/* 点击marker进行操作，进行当前位置和点击位置的连线 */
		markerEvent(e) {
			this.marker = e.target.markerId;
			this.polyline[0].points = [];
			this.polyline[0].points.push({
				latitude: this.covers[0].latitude,
				longitude: this.covers[0].longitude
			});
			this.polyline[0].points.push({
				latitude: this.covers[e.target.markerId - 1].latitude,
				longitude: this.covers[e.target.markerId - 1].longitude
			})
		},
		/* control点击事件 */
		controlEvent(e) {
			var id = e.mp.controlId;
			switch (id) {
				case 1:
					/* 第一个control */
					this.map.moveToLocation()
					break;
				case 2:
					/* 第二个control */
					this.getCurrentLocation();
					setTimeout(() => {
						if (this.polyline[0].points) {
							this.polyline[0].points[0].latitude = this.covers[0].latitude;
							this.polyline[0].points[0].longitude = this.covers[0].longitude;
						}
					}, 300)
					break;
				case 3:
					/* 第三个control */
					// if (this.marker == null) {
					// 	uni.showToast({
					// 		icon: 'none',
					// 		title: `导航失败，请先选择目的地`
					// 	});
					// } else {
					// 	let longitude = this.covers[this.marker - 1].longitude;
					// 	let latitude = this.covers[this.marker - 1].latitude;
					// 	let title = this.covers[this.marker - 1].title;
					// 	/* 调用导航函数 */
					this.navigation();
					// }
					break;
				default:
					console.log('无操作')
					break;
			}
		},
		/* 获取当前位置 */
		getCurrentLocation() {
			uni.getLocation({
				type: 'gcj02', //获取的位置准确
				success: (res) => {
					console.log('当前位置的经度：' + res.longitude);
					console.log('当前位置的纬度：' + res.latitude);
					this.covers[0].latitude = res.latitude;
					this.covers[0].longitude = res.longitude;
				}
			});
		},
		/* 导航 */
		navigation() {
			console.log('调用')
			uni.openLocation({
				latitude: Number(this.latitude),
				longitude: Number(this.longitude),
				name: this.name,
				address: this.name,
				success: function() {
					console.log('success');
				}
			})
		},
		/* 去重函数（去除相同经纬度的数据） */
		removeSame(data) {
			let array = [];
			console.log(data.length)
			for (let j = 0; j < data.length; j++) {
				let status = true;
				for (let i = 0; i < array.length; i++) {
					console.log(array)
					if (data[j].businessId.position.longitude === array[i].longitude && data[j].businessId.position.latitude ===
						array[i].latitude) {
						status = false;
						console.log(i)
					}
				}
				if (status) {
					array.push(data[j].businessId.position)
				}
			}
			return array
		}
	}
}
